@charset "UTF-8";
.main-header {
  min-height: 56px;
  display: flex;
}

.main-header__logo {
  position: absolute;
  z-index: 2;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.page {
  margin: 0;
  padding: 0;
  background-color: #f2f2f2;
  font-family: "PT Sans", "Arial", sans-serif;
}

.page__body {
  margin: 0 auto;
  padding: 0;
  max-width: 1200px;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}

.page__main {
  flex-grow: 1;
}

.page__footer {
  min-height: 235px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  gap: 30px;
}
@media (min-width: 768px) {
  .page__footer {
    min-height: 120px;
    flex-direction: row;
    justify-content: space-between;
    padding: 0 60px;
  }
}
@media (min-width: 1200px) {
  .page__footer {
    padding: 0 120px;
  }
}

.page__main-title {
  margin: 0;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
  margin-top: 15px;
}
@media (min-width: 1200px) {
  .page__main-title {
    padding: 0;
    margin-top: 0;
  }
}

.page__modal {
  display: none;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 1;
}

.page__modal-button {
  font-family: "PT Sans", "Arial", sans-serif;
  background-color: #81b3d2;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  color: #ffffff;
  padding: 16px;
  border: none;
  text-transform: uppercase;
  margin: 0;
  cursor: pointer;
  outline: none;
}
.page__modal-button:hover {
  background-color: #669ec0;
}
.page__modal-button:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
.page__modal-button:focus {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
.page__modal-button:disabled {
  background-color: #e5e5e5;
  color: #ffffff;
}

.page__modal-button--ok {
  width: calc(100% - 140px);
  display: block;
  margin: 0 70px 35px;
}
@media (min-width: 768px) {
  .page__modal-button--ok {
    width: calc(100% - 128px);
    margin: 0 64px 49px;
  }
}

.page__modal-button--close {
  width: 100%;
}

.page__failure {
  background-color: #ffffff;
  width: 100%;
  min-height: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
}
@media (min-width: 768px) {
  .page__failure {
    width: 400px;
  }
}

.page__failure-content h3 {
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .page__failure-content h3 {
    font-size: 30px;
    line-height: 40px;
  }
}

.page__failure-content p {
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .page__failure-content p {
    font-size: 21px;
    line-height: 26px;
  }
}

.page__failure-content {
  padding: 45px 42px 28px;
}

.page__success {
  background-color: #ffffff;
  width: 100%;
  min-height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
}
@media (min-width: 768px) {
  .page__success {
    width: 640px;
  }
}

.page__success-content {
  padding: 45px 42px 30px;
}

.page__success-content h3 {
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .page__success-content h3 {
    font-size: 30px;
    line-height: 40px;
  }
}

.page__success-content p {
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .page__success-content p {
    font-size: 21px;
    line-height: 26px;
  }
}

.page__success-button {
