.main-nav {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 100%;
  width: 100%;
  padding-right: 20px;
  position: relative;
}
@media (min-width: 768px) {
  .main-nav {
    padding-right: 0;
  }
}

.main-nav__wrapper {
  display: flex;
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 3;
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    position: relative;
    display: flex;
    z-index: 1;
  }
}

.main-nav__list {
  padding: 0;
  margin: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .main-nav__list {
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.main-nav__toggle-wrapper {
  width: 55px;
  height: 55px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  right: 0;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
@media (min-width: 768px) {
  .main-nav__toggle-wrapper {
    display: none;
  }
}
.main-nav__toggle-wrapper:hover .main-nav__toggle {
  opacity: 0.6;
}
.main-nav__toggle-wrapper:active .main-nav__toggle {
  opacity: 0.3;
}

.main-nav__toggle {
  display: flex;
  flex-direction: column;
  gap: 4px;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  position: relative;
  margin: 0;
}
.main-nav__toggle::before {
  content: "";
  display: flex;
  width: 23px;
  min-height: 4px;
  transform: translateY(-8px);
  background-color: #81b3d2;
}
.main-nav__toggle::after {
  content: "";
  display: flex;
  width: 23px;
  min-height: 4px;
  background-color: #81b3d2;
}

.main-nav__close-menu {
  position: absolute;
  height: 55px;
  width: 55px;
  top: 0;
  right: 0;
  background-image: url("../img/icon-cross.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-color: transparent;
  border: none;
  z-index: 2;
  cursor: pointer;
}
@media (min-width: 768px) {
  .main-nav__close-menu {
    display: none;
  }
}
.main-nav__close-menu:hover {
  opacity: 0.6;
}
.main-nav__close-menu:active {
  opacity: 0.3;
}

